'use scrict';
import { formatDate } from '@/config/filter.js';

export default {
	data() {
		return {
			list: [{info: "奖励积分: 10", desc: "奖励时间: 2020/01/10"}],
			isShowContent: false
		}
	},
	props: {
		infor: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	filters: {
		formatDate
	},
	computed: {
		roles() {
			return this.infor.roleName && this.infor.roleName.split(',');
		}
	},
	methods: {
		changeContent() {
			this.isShowContent = !this.isShowContent;
		}
	}
}
